import React from "react";
import FooterStyles from "./FooterStyles";
import contactList from '../../content/contact.json'

const displayContactElements = (contactList) => {
  return contactList.keys().map((element, i) => {
    return element === "Dirección" ? <a href="https://goo.gl/maps/K6zbJ39jVTn" target="_blank">
    Dirección: Avenida del Padre Piquer, 39, 28024 Madrid
  </a> : <li><p>{element}</p>
  <p>contactList[element]</p></li>
  })
}

const Footer = () => {
  return (
    <FooterStyles>
      {displayContactElements(contactList)}
      {/* <a href="https://goo.gl/maps/K6zbJ39jVTn" target="_blank">
        Dirección: Avenida del Padre Piquer, 39, 28024 Madrid
      </a> */}
    </FooterStyles>
  );
};

export default Footer;
